import React from "react";
import { Box, Button, Typography } from "@mui/material";
import PDF from "../../assets/Kalagotla_Sai_Resume.pdf";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import LightModeIcon from "@mui/icons-material/LightMode";
import { useDispatch } from "react-redux";
import { changeMode } from "../../colorModeSlice";

const Link = ({ text, id, mode }) => {
  return (
    <Button
      sx={{
        backgroundColor: mode === "dark" ? "#252F45" : "#DBEAF6",
        color: mode === "dark" ? "#C2BAD2" : "#6C5380",
      }}
      onClick={() => {
        const element = document.getElementById(id);
        element?.scrollIntoView({
          behavior: "smooth",
        });
      }}
    >
      <Typography variant="body1" sx={{ fontSize: "15px" }}>
        <Box sx={{ fontWeight: "medium", textTransform: "capitalize" }}>
          {text}
        </Box>
      </Typography>
    </Button>
  );
};

const Navbar = ({ mode }) => {
  let dispatch = useDispatch();

  return (
    <Box
      sx={{
        height: "10%",
        width: "100%",
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "space-between",
        padding: "10px 50px 10px 50px",
        position: "sticky",
        top: "0",
        backgroundColor: mode === "dark" ? "#1B2335" : "#EEF8FD",
        zIndex: 1,
      }}
    >
      <Box>
        <Button
          sx={{ backgroundColor: mode === "dark" ? "#252F45" : "#DBEAF6" }}
          onClick={() => {
            dispatch(changeMode());
          }}
        >
          {mode === "dark" ? (
            <LightModeIcon sx={{ color: "#847798" }} />
          ) : (
            <DarkModeIcon sx={{ color: "#36367B" }} />
          )}
        </Button>
      </Box>
      <Box
        sx={{
          height: "100%",
          width: "50%",
          display: "flex",
          justifyContent: "flex-end",
          gap: "20px",
        }}
      >
        <Link text="About" id="about" mode={mode} />
        <Link text="Projects" id="projects" mode={mode} />
        <Button
          sx={{
            backgroundColor: mode === "dark" ? "#252F45" : "#DBEAF6",
            color: mode === "dark" ? "#C2BAD2" : "#6C5380",
          }}
          href={PDF}
          target="_blank"
        >
          <Box sx={{ fontWeight: "medium", textTransform: "capitalize" }}>
            Resume
          </Box>
        </Button>
      </Box>
    </Box>
  );
};

export default Navbar;
