import React from "react";
import { Box, Typography } from "@mui/material";

function Project({ mode, imageSrc, description, index }) {
  console.log(imageSrc);
  return (
    <Box
      sx={{
        height: "100%",
        width: "100%",
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "Center",
        padding: "10%",
        gap: "5%",
      }}
    >
      {index % 2 === 0 ? (
        <img src={imageSrc} style={{ width: "50%" }} />
      ) : (
        <Typography
          sx={{
            fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
            color: mode === "dark" ? "#A9A3C1" : "#575380",
          }}
          fontFamily={"sans-serif"}
        >
          {description}
        </Typography>
      )}
      {index % 2 === 0 ? (
        <Typography
          sx={{
            fontSize: { xs: "15px", sm: "15px", md: "15px", lg: "20px" },
            color: mode === "dark" ? "#A9A3C1" : "#575380",
          }}
          fontFamily={"sans-serif"}
        >
          {description}
        </Typography>
      ) : (
        <img src={imageSrc} style={{ width: "50%" }} />
      )}
    </Box>
  );
}

export default Project;
